.adder-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 1.6rem;
}

.adder-wrapper .header {
    text-align: center;
    color: white;
    /* cyan500 -> cyan200 light */
    background: -webkit-radial-gradient(circle farthest-side, #00bcd4, #80deea);
    background: -o-radial-gradient(circle farthest-side, #00bcd4, #80deea);
    background: radial-gradient(circle farthest-side, #00bcd4, #80deea);
    margin: -1.6rem;
    margin-bottom: 1.6rem;
    padding: 1.6rem;
    padding-bottom: calc(1.6rem + 32px);
    position: relative;
}

.adder-wrapper .header a {
    color: white !important;
}

.adder-wrapper .header .logo-wrapper {
    background-color: #ff4081;
    border-radius: 50%;
    width: 64px;
    height:64px;
    padding: 1rem;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 8px);
        -ms-transform: translate(-50%, 8px);
            transform: translate(-50%, 8px);
}

.adder-wrapper .content {
    margin-top: calc(1.6rem + 32px);
}

.adder-wrapper .content .create-button {
    margin-top: .8rem !important;
}



