.error-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.error-wrapper .error-message {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 1.6rem;
}


