.static-slide {
    width: 200px;
    height: 113px; /* 16:9 */
    position: relative;
    margin: 1rem 0  0 1rem;
    cursor: pointer;
    display: inline-block;
}

.static-slide .title {
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    bottom:0;
    width: calc(100% - 2rem);
    padding: 1rem;
    font-size: 1.3rem;
    color: white;
}

.static-slide.active .title {
    background-color: rgba(0, 188, 212, 0.6) !important;
}

.static-slide .remove-button {
    position: absolute !important;
    top:0;
    right:0;
    -webkit-transform: translate(50%, -50%);
        -ms-transform: translate(50%, -50%);
            transform: translate(50%, -50%);
}


